import ReactDOM from 'react-dom';
import React, {useState, useEffect} from 'react';
import Cropper from 'cropperjs';
import debounce from 'lodash/debounce';
import './ProfileImageWatermarker.css';
import '../node_modules/cropperjs/dist/cropper.css'
const watermark = require('watermarkjs')


const watermarkOptions = ['/watermarks/yang1.png','/watermarks/yang2.png','/watermarks/yang3.png']
let cropper = ''

function ProfileImageWatermarker() {
  const [selectedWatermark, setSelectedWatermark] = useState(0);
  const [watermarkLevel, setWatermarkLevel] = useState(0.75);
  const [currentDisplay, setcurrentDisplay] = useState(1);

  useEffect( () => {
      buildCropper();
    },[]);

  const changeWaterMark = (e) => {
    setSelectedWatermark(parseInt(e.target.id))
  }

  const adjustWatermarkLevel = (e) => {
    setWatermarkLevel(e.target.value / 100)
  }

  const buildCropper = () => {
    let cropBox = document.querySelector('#cropImage')
    let cropPreview = document.querySelector('#cropPreview')
    cropper = new Cropper(cropBox, {
        aspectRatio: 1,
        minCropBoxWidth: 100,
        dragMode: 'move',
        cropBoxMovable: false,
        crop: debounce(() => {
          let canvas = cropper.getCroppedCanvas()
          cropPreview.src = canvas.toDataURL('image/png')
        }, 5)
      });
  }

  const buildWatermarkOptions = watermarkOptions.map( (i,j) => {
      return <img className='watermarkOption' id={`${j}watermarkChoice`} key={`watermarkoption${j}`} alt={i} src={process.env.PUBLIC_URL + i} onClick={changeWaterMark} />
    })

  const makeCropper = (e) => {
      e.preventDefault()
      let upload = e.target.files;
      if(upload.length === 0) return;
      let cropBox = document.querySelector('#cropImage');

      if(!upload[0].name.endsWith('.jpg') && !upload[0].name.endsWith('.png')) {
        alert('Please select a .jpg or .png file.')
        return
      }

      cropper.destroy()

      let reader = new FileReader();
      reader.onload = r => {
          cropBox.alt= 'Image Editor Canvas'
          cropBox.src = r.target.result;
          buildCropper();
          };
          cropBox.alt= 'Loading Image...'
          cropBox.src = '';
      reader.readAsDataURL(upload[0]);
  };

  const createImage = (e) => {
    e.preventDefault()
    let cropSelection = cropper.getCroppedCanvas({
      //width: 512, //doesn't work??
      minWidth: 512,
      maxWidth: 512,
      imageSmoothingQuality: 'high'
    }).toDataURL()

    watermark([cropSelection, process.env.PUBLIC_URL + watermarkOptions[selectedWatermark]])
      .dataUrl(watermark.image.center(watermarkLevel))
      .then(function (url) {
        let downloadAnchor = document.querySelector('#downloadLink');
        downloadAnchor.setAttribute('href', url)
        downloadAnchor.setAttribute('download', 'YANG2020');
        setcurrentDisplay(2)
        document.querySelector('#resultImage').src = url
      });
    };

    const goBack = () => {
      setcurrentDisplay(1)
    }

  return (
    <div id='ProfileImageWatermarker'>
      <header>
        <picture>
          <source media='(max-width: 749px)' srcset={process.env.PUBLIC_URL + '/headerSmall.png'} />
          <source media='(min-width: 750px)' srcset={process.env.PUBLIC_URL + '/headerLarge.png'} />
          <img src={process.env.PUBLIC_URL + '/headerLarge.png'} alt='Yang2020 Banner' />
        </picture>
      </header>

      <div id='infoContainer'>
        { currentDisplay === 1 &&
          <>
            <p>Show your support on social media with a Yang2020 watermarked avatar.</p>
            <label className='actionButton'>
              <h2><i class="fas fa-cloud-upload-alt"></i> UPLOAD</h2>
              <input id='photoUpload' type='file' accept='.png, .jpg' name='photo' onChange={makeCropper} hidden />
            </label>
          </> }
          <p style={ {'display': currentDisplay === 2 ? 'block' : 'none'} }>Download your new Yang2020 avatar and upload to your social platforms.</p>
          <a id='downloadLink' className='actionButton' href='empty' download='' style={ {'display': currentDisplay === 2 ? 'flex' : 'none'} }>
            <h2><i class="fas fa-cloud-download-alt"></i> DOWNLOAD</h2>
          </a>

      </div>
      <div id='headerContainer'>
        <h3 className='headerLeft'>{currentDisplay === 1 ? 'Drag/Zoom/Crop' : 'Upload your image:' }</h3>
        <h3 className='headerRight'>{currentDisplay === 1 ? 'Preview' : 'Result' }</h3>
      </div>
      <div id='imageContainer'>
        <div className='cropBox' style={ {'display': currentDisplay === 1 ? 'block' : 'none'} }>
          <img id='cropImage' src={process.env.PUBLIC_URL + '/startImage.jpg'} alt='Editor Canvas' />
        </div>
        <div className='cropBox' style={ {'display': currentDisplay === 2 ? 'block' : 'none'} }>
          <ul>
            <li><a href='https://twitter.com/settings/profile' target='_blank' rel="noopener noreferrer"><i class="fab fa-twitter"></i> Twitter</a></li>
            <li><a href='https://facebook.com/settings/profile' target='_blank' rel="noopener noreferrer"><i class="fab fa-facebook-f"></i> Facebook (??)</a></li>
            <li><a href='https://support.discordapp.com/hc/en-us/articles/204156688-How-do-I-change-my-avatar-' target='_blank' rel="noopener noreferrer"><i class="fab fa-discord"></i> Discord</a></li>
            <li><a href='https://counter.social/settings/profile' target='_blank' rel="noopener noreferrer"><i class="fas fa-fire-extinguisher"></i> CounterSocial</a></li>
          </ul>
        </div>

        <div className='previewBox' style={ {'display': currentDisplay === 1 ? 'block' : 'none'} }>
          <img id='cropPreview' src={process.env.PUBLIC_URL + '/startImage.jpg'} alt='Cropped Preview' />
          <img id='cropWatermark' src={process.env.PUBLIC_URL + watermarkOptions[selectedWatermark]} style={ { opacity: watermarkLevel } } alt='Watermark Preview' />
        </div>
        <div className='previewBox' style={ {'display': currentDisplay === 2 ? 'block' : 'none'} }>
          <img id='resultImage' src='' alt='Final Preview' />
        </div>

      </div>

      {currentDisplay === 1 &&
      <div id='watermarkOptions'>
         Choose a watermark:
         {buildWatermarkOptions}
      </div>
      }

      { currentDisplay === 1 &&
      <div id="transparencySlider">
        <input type="range" min="10" max="100" value={Math.round(watermarkLevel * 100)} className="slider" onInput={adjustWatermarkLevel} />
        <p>Watermark Transparency: {Math.round(watermarkLevel * 100)}%</p>
      </div>
      }
      <div className='navContainer'>
      { currentDisplay === 1 &&
        <div className='navButton' onClick={createImage}><p>CREATE <i class="far fa-hand-point-right"></i></p></div>
      }
      { currentDisplay === 2 &&
        <>
        <div className='navButton' onClick={goBack}><p><i class="far fa-hand-point-left"></i> BACK</p></div>
    {  //  <a className='navButton' href='https://discord.gg/UqmqzfB' target='_blank' rel="noopener noreferrer"><p>Yang202 Discord</p></a>
    }
        </>
      }
      </div>
    </div>
  );
}

ReactDOM.render(<ProfileImageWatermarker />, document.getElementById('root'));
